@use "sass:map";
@use "../settings" as *;

@if map.get($modules, "components/dropdown") and $enable-classes {
  /**
   * Dropdown (details.dropdown)
   */

  // Container
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  details.dropdown {
    position: relative;
    border-bottom: none;

    // Marker
    // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
    summary,
    > button,
    > a {
      &::after {
        display: block;
        width: 1rem;
        height: calc(1rem * var(#{$âœ¨}line-height, 1.5));
        margin-inline-start: 0.25rem;
        float: right;
        // TODO: find out why we need this magic number (0.2 rem)
        // for the marker to be aligned with the regular select
        transform: rotate(0deg) translateX(0.2rem);
        background-image: var(#{$âœ¨}icon-chevron);
        background-position: right center;
        background-size: 1rem auto;
        background-repeat: no-repeat;
        content: "";
      }
    }
  }

  // Container type accordion
  // inside a nav
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  nav details.dropdown {
    // Override height
    margin-bottom: 0;
  }

  // Bouton as a select
  // inside container type accordion
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  details.dropdown summary:not([role]) {
    height: calc(
      1rem *
        var(#{$âœ¨}line-height) +
        var(#{$âœ¨}form-element-spacing-vertical) *
        2 +
        var(#{$âœ¨}border-width) *
        2
    );
    padding: var(#{$âœ¨}form-element-spacing-vertical) var(#{$âœ¨}form-element-spacing-horizontal);
    border: var(#{$âœ¨}border-width) solid var(#{$âœ¨}form-element-border-color);
    border-radius: var(#{$âœ¨}border-radius);
    background-color: var(#{$âœ¨}form-element-background-color);
    color: var(#{$âœ¨}form-element-placeholder-color);
    line-height: inherit;
    cursor: pointer;
    user-select: none;

    @if $enable-transitions {
      transition: background-color var(#{$âœ¨}transition), border-color var(#{$âœ¨}transition),
        color var(#{$âœ¨}transition), box-shadow var(#{$âœ¨}transition);
    }

    &:active,
    &:focus {
      border-color: var(#{$âœ¨}form-element-active-border-color);
      background-color: var(#{$âœ¨}form-element-active-background-color);
    }

    &:focus {
      box-shadow: 0 0 0 var(#{$âœ¨}outline-width) var(#{$âœ¨}form-element-focus-color);
    }

    // Reset focus visible from accordion component
    &:focus-visible {
      outline: none;
    }

    // Aria-invalid
    &[aria-invalid="false"] {
      #{$âœ¨}form-element-border-color: var(#{$âœ¨}form-element-valid-border-color);
      #{$âœ¨}form-element-active-border-color: var(#{$âœ¨}form-element-valid-focus-color);
      #{$âœ¨}form-element-focus-color: var(#{$âœ¨}form-element-valid-focus-color);
    }

    &[aria-invalid="true"] {
      #{$âœ¨}form-element-border-color: var(#{$âœ¨}form-element-invalid-border-color);
      #{$âœ¨}form-element-active-border-color: var(#{$âœ¨}form-element-invalid-focus-color);
      #{$âœ¨}form-element-focus-color: var(#{$âœ¨}form-element-invalid-focus-color);
    }
  }

  // Dropdown inside a nav
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  nav details.dropdown {
    display: inline;
    margin: calc(var(#{$âœ¨}nav-element-spacing-vertical) * -1) 0;

    summary:not([role]) {
      // Override height
      height: calc((1rem * var(#{$âœ¨}line-height)) + (var(#{$âœ¨}nav-link-spacing-vertical) * 2));
      padding: calc(var(#{$âœ¨}nav-link-spacing-vertical) - (var(#{$âœ¨}border-width) * 2))
        var(#{$âœ¨}nav-link-spacing-horizontal);

      &:focus-visible {
        box-shadow: 0 0 0 var(#{$âœ¨}outline-width) var(#{$âœ¨}primary-focus);
      }
    }
  }

  // Submenu
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  details.dropdown summary + ul {
    display: flex;
    z-index: 99;
    position: absolute;
    left: 0;
    flex-direction: column;
    width: 100%;
    min-width: fit-content;
    margin: 0;
    margin-top: var(#{$âœ¨}outline-width);
    padding: 0;
    border: var(#{$âœ¨}border-width) solid var(#{$âœ¨}dropdown-border-color);
    border-radius: var(#{$âœ¨}border-radius);
    background-color: var(#{$âœ¨}dropdown-background-color);
    box-shadow: var(#{$âœ¨}dropdown-box-shadow);
    color: var(#{$âœ¨}dropdown-color);
    white-space: nowrap;
    opacity: 0;

    @if $enable-transitions {
      transition: opacity var(--pico-transition), transform 0s ease-in-out 1s;
    }

    &[dir="rtl"] {
      right: 0;
      left: auto;
    }

    li {
      width: 100%;
      margin-bottom: 0;
      padding: calc(var(#{$âœ¨}form-element-spacing-vertical) * 0.5)
        var(#{$âœ¨}form-element-spacing-horizontal);
      list-style: none;

      &:first-of-type {
        margin-top: calc(var(#{$âœ¨}form-element-spacing-vertical) * 0.5);
      }

      &:last-of-type {
        margin-bottom: calc(var(#{$âœ¨}form-element-spacing-vertical) * 0.5);
      }

      a {
        display: block;
        margin: calc(var(#{$âœ¨}form-element-spacing-vertical) * -0.5)
          calc(var(#{$âœ¨}form-element-spacing-horizontal) * -1);
        padding: calc(var(#{$âœ¨}form-element-spacing-vertical) * 0.5)
          var(#{$âœ¨}form-element-spacing-horizontal);
        overflow: hidden;
        border-radius: 0;
        color: var(#{$âœ¨}dropdown-color);
        text-decoration: none;
        text-overflow: ellipsis;

        &:hover,
        &:focus,
        &:active,
        &:focus-visible,
        &[aria-current] {
          background-color: var(#{$âœ¨}dropdown-hover-background-color);
        }
      }

      label {
        width: 100%;
      }

      // Not working in Firefox, which doesn't support the `:has()` pseudo-class
      &:has(label):hover {
        background-color: var(#{$âœ¨}dropdown-hover-background-color);
      }
    }
  }

  // Button opened
  // inside container type accordion
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  details.dropdown[open] summary {
    margin-bottom: 0;
  }

  // Menu opened
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  // 1. Inside container type accordion
  details.dropdown[open] summary {
    + ul {
      transform: scaleY(1);
      opacity: 1;

      @if $enable-transitions {
        transition: opacity var(--pico-transition), transform 0s ease-in-out 0s;
      }
    }
  }

  // Close for dropdown
  // inside container type accordion
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  details.dropdown[open] summary {
    &::before {
      display: block;
      z-index: 1;
      position: fixed;
      width: 100vw;
      height: 100vh;
      inset: 0;
      background: none;
      content: "";
      cursor: default;
    }
  }
  // Label
  // â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“â€“
  label > details.dropdown {
    margin-top: calc(var(#{$âœ¨}spacing) * 0.25);
  }
}
